	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/fph0123.jpg\" height=\"300\" width=\"400\" border=\"0\" alt=\"fph0123.jpg: \" /></p>\n<p>After a wet and sloppy snow day yesterday, it turned cold and clear last night. We bundled up Christopher and went for <a href=\"http://shanson.editthispage.com/stories/storyReader$109\">a walk</a>.</p>\n<p>Christopher felt right at home at the party last night. We stayed over an hour. When we got home, Mama and I were went straight to bed, but Christopher was ready for more! (Note for our to-do list: teach Christopher the difference between day and night.)</p>","fields":{"title":"Sunday Walk","date":"23 Jan 2000","url":"https://papascott.de/2000-01-23-sunday-walk/"}}},"pathContext":{"slug":"/2000-01-23-sunday-walk/","prev":{"fields":{"title":"Christopher Goes Partying","date":"22 Jan 2000","slug":"/2000-01-22-christopher-goes-partying/"}},"next":{"fields":{"title":"Monday Monday","date":"24 Jan 2000","slug":"/2000-01-24-monday-monday/"}}}}

